body {
  font-size: 14px;
  line-height: 18px;
  font-weight: normal;
  padding: 0;
  margin: 0;
  min-width: 320px;
  font-family: "Arial", sans-serif;
  font-style: normal;
  box-sizing: border-box;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.content-wrapper {
  width: 1200px;
  margin: 0 auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"],
input[type="radio"],
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header {
  background-color: #444444;
  margin-bottom: 20px; }

.site-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  padding: 0 8%; }

.site-list__item {
  padding: 10px 0; }
  .site-list__item a {
    font-size: 16px;
    line-height: 16px;
    font-weight: bold;
    color: #cdcdcd; }
    .site-list__item a:hover {
      color: rgba(204, 204, 204, 0.6); }
    .site-list__item a:active {
      color: rgba(204, 204, 204, 0.3); }
  .site-list__item--catalog {
    position: relative; }

.catalog-list {
  background-color: #6b6868;
  position: absolute;
  min-width: 200px;
  top: 38px;
  left: 0;
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0;
  padding: 0; }

.catalog-list__item {
  margin: 0;
  padding: 0;
  background: #6b6868; }
  .catalog-list__item a {
    font-size: 16px;
    line-height: 16px;
    font-weight: bold;
    color: #cdcdcd; }
    .catalog-list__item a:hover {
      color: rgba(204, 204, 204, 0.6); }
    .catalog-list__item a:active {
      color: rgba(204, 204, 204, 0.3); }
  .catalog-list__item a {
    display: flex;
    padding: 10px; }

.catalog-list__submenu {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0;
  padding: 0; }

.catalog-list__submenu-link {
  background-color: #68b738; }
  .catalog-list__submenu-link a {
    font-size: 16px;
    line-height: 16px;
    font-weight: bold;
    color: #cdcdcd; }
    .catalog-list__submenu-link a:hover {
      color: rgba(204, 204, 204, 0.6); }
    .catalog-list__submenu-link a:active {
      color: rgba(204, 204, 204, 0.3); }

.title {
  font-size: 36px;
  line-height: 36px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px; }

.product-title a {
  color: #111111; }
  .product-title a:hover {
    color: #444444; }
  .product-title a:active {
    color: rgba(69, 69, 69, 0.6); }

.catalog-flex {
  display: flex;
  flex-direction: column-reverse;
  margin-bottom: 20px; }

.catalog-flex__row {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  min-height: 450px;
  justify-content: flex-end;
  flex-direction: row-reverse; }

.product-flex {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .product-flex--align {
    align-self: center; }

.catalog-inline {
  list-style: none;
  margin: 0;
  padding: 0;
  padding: 0 8%;
  margin-bottom: 20px; }

.product-inline {
  width: 247px;
  display: inline-block;
  text-align: center;
  margin-left: -.36em;
  background-color: #68b738; }
  .product-inline:nth-child(4n+1) {
    margin-left: 0; }

.product-inline__link {
  display: block; }

.catalog-float {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
  padding: 0 5%; }
  .catalog-float::after {
    display: table;
    clear: both;
    content: ''; }

.product-float {
  float: left;
  width: calc((100% - 20px * 4) / 4);
  margin-right: 10px;
  margin-left: 10px; }

.product-float__title {
  text-align: center; }

.product-float__link {
  display: block; }

.catalog-table {
  margin-bottom: 20px;
  width: 100%; }

.product-table {
  margin-bottom: 20px; }

.product-table__title {
  text-align: center; }

.product-table__link {
  display: block; }

.product-table__image {
  width: 100%; }

.catalog-div {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 20px; }

.product-div {
  display: flex;
  flex-direction: column;
  align-items: center; }

.footer {
  min-height: 90px;
  background-color: #444444; }
